body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}






.row-col{
  display: flex;
}

.col {
  flex: 1; /* Las columnas ocupan el espacio disponible por igual */
}

.w-90 {
  width: 80%; /* Ancho de la columna 1 */
}

.w-5 {
  width: 10%; /* Ancho de la columna 2 */
  background-color: blue;
}

.w-6 {
  width: 10%; /* Ancho de la columna 3 */
  background-color: brown;
}

