body{
  font-family:Verdana,sans-serif;
}
.content{
  margin: auto;
  max-width: 600px;
  padding: 0.01em 16px;
}

.App {
  padding-top: 100px;
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

.form-container {
  background: linear-gradient(#212121, #212121) padding-box,
              linear-gradient(145deg, transparent 35%,#e81cff, #40c9ff) border-box;
  border: 2px solid transparent;
  padding: 32px 24px;
  font-size: 14px;
  font-family: inherit;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 20px;
  box-sizing: border-box;
  border-radius: 16px;
  background-size: 200% 100%;
  animation: gradient 5s ease infinite;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.form-container .form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-container button:active {
  scale: 0.95;
}

.form-container .form-group {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.form-container .form-group label {
  display: block;
  margin-bottom: 5px;
  color: #717171;
  font-weight: 600;
  font-size: 12px;
  text-align: left;
}

.form-container .form-group input {
  padding: 12px 16px;
  border-radius: 8px;
  color: #fff;
  font-family: inherit;
  background-color: transparent;
  border: 1px solid #414141;
}

.impot{
  padding: 12px 16px;
  border-radius: 8px;
  color: #fff;
  font-family: inherit;
  background-color: transparent;
  border: 1px solid #414141;
  width: 100%;
}

.impot::placeholder {
  opacity: 0.5;
}

.impot:focus {
  outline: none;
  border-color: #e81cff;
}

.p-r{
  padding-right: 8px;
}

.form-container .form-group input::placeholder {
  opacity: 0.5;
}

.form-container .form-group input:focus {
  outline: none;
  border-color: #e81cff;
}

.form-container .form-submit-btn {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  align-self: flex-start;
  font-family: inherit;
  color: #717171;
  font-weight: 600;
  background: #313131;
  border: 1px solid #414141;
  padding: 12px 16px;
  font-size: inherit;
  gap: 8px;
  cursor: pointer;
  border-radius: 6px;
  width: 100%;
}

.form-container .form-submit-btn:hover {
  background-color: #fff;
  border-color: #fff;
}

@media (min-width:993px){
  .App{
    padding-top: 30px;
  }
}





