

.input-trade label {
    display: block;
    margin-bottom: 5px;
    color: #717171;
    font-weight: 600;
    font-size: 12px;
    text-align: left;
}

.input-trade-btn {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    align-self: flex-start;
    font-family: inherit;
    color: #717171;
    font-weight: 600;
    background: #313131;
    border: 1px solid #414141;
    padding: 12px 16px;
    font-size: inherit;
    gap: 8px;
    cursor: pointer;
    border-radius: 6px;
    width: 100%;
}

.input-trade-price{
    padding-top: 14px;
}

.input-trade-pad{
    padding-left: 5px;
    padding-right: 5px;
}

.input-trade-input{
    width: 100%;
    display: block;
    clear: both;
    padding: 12px 16px;
    border-radius: 8px;
    color: #fff;
    font-family: inherit;
    background-color: transparent;
    border: 1px solid #414141;
}